import { Wrapper, Label, InputContainer, InputContent } from './style'

const SelectCheckBox = ({ name, items, rounded }) => {
  const renderItems = () => {
    return items.map((item) => {
      return (
        <>
          <InputContainer>
            <InputContent rounded={rounded} type="checkbox" id={item} value={item}/>
            <span>{item}</span>
          </InputContainer>
        </>
      )
    })
  }
  return (
    <Wrapper>
      <Label>{name}</Label>
      {renderItems()}
    </Wrapper>
  )
}

export default SelectCheckBox
